*{margin: 0;padding: 0;box-sizing: border-box;}
header{
  background-color: aliceblue;
  padding: 20px 10px;
  font-weight: bold;
}
.list{
  padding: 20px 10px;
  border: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
}
.listname{
  width: calc(100% - 100px);
  color: deepskyblue;
}
.good {width: 100px;
}
.good>span{cursor: pointer;}
.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}
.modal  {
  background-color: #eee;
  padding: 20px;
  position: absolute;
  top: 100px;
  right: 0;
  width: 50%;
}

.modal2{
  top: 200px;
  background-color: aliceblue;
}